import React from "react";
import SwiperCore, { Autoplay, Navigation } from "swiper";
import { Swiper, SwiperSlide } from "swiper/react";
import ScrollAnimation from "react-animate-on-scroll";

import juanHigaImage from '../../assets/images/quotes/juanHiga.png';
import kentaHeiannaImage from '../../assets/images/quotes/kentaHeianna.png';
import jenniferOshiroImage from '../../assets/images/quotes/jenniferOshiro.png';

const quotes = [
    {
        author: "Juan Higa",
        image: juanHigaImage,
        quote: "Un sueño hecho realidad que el próximo confra se haga en Argentina 2023!",
        description: "Delegación Argentina de Tenis de campo"
    },
    {
        author: "Kenta Heianna",
        image: kentaHeiannaImage,
        quote: "No puedo esperar! Ansío que hayan mucho chicos lindos que les guste el kpop!",
        description: "Delegación Argentina de Kpop"
    },
    {
        author: "Jennifer Oshiro",
        image: jenniferOshiroImage,
        quote: "Tengo muchas ganas de demostrar los resultados de mi entrenamiento, y qué mejor manera de hacerlo que en mi propio país, en la competencia Nikkei más grande del mundo!",
        description: "Delegación Argentina de traga monedas"
    },
];

const Quotes = () => {
    return (
        <section
            id="quotes"
            style={{ marginTop: -2, zIndex: 2 }}
            className="pb-5 p-4 position-relative"
        >
            <div className="container position-relative pb-5 mb-5 text-center" >
                <div className="position-relative">                    
                    <Swiper
                        navigation={{
                            nextEl: ".btn-next",
                            prevEl: ".btn-prev",
                        }}
                        slidesPerView={1}
                        spaceBetween={30}
                        autoplay={{
                            delay: 3500,
                            disableOnInteraction: false,
                            pauseOnMouseEnter: true,
                        }}
                        effect="cube"
                        loop
                    >
                        {quotes.map((place, index) => (
                            <SwiperSlide key={index} className="p-3 rounded-3">
                                <div className="row position-relative">
                                    <div className="position-relative col-lg-4 rounded-3 overflow-hidden">
                                        <ScrollAnimation animateOnce animateIn="fadeInLeft">
                                            <img
                                                src={place.image}
                                                alt={place.author}
                                                className="image aspect rounded-pill mb-3"
                                                style={{ verticalAlign: 'middle' }}
                                            />
                                        </ScrollAnimation>
                                    </div>
                                    <div className="px-3 col-lg-8 d-flex justify-content-center flex-column">
                                        <ScrollAnimation animateOnce animateIn="fadeInRight">
                                            <em>{place.quote}</em>
                                            <br /><br />
                                            <p className="righteous">
                                                <strong>{place.author} - </strong>
                                                <em>{place.description}</em>
                                            </p>
                                        </ScrollAnimation>
                                    </div>
                                </div>
                            </SwiperSlide>
                        ))}
                    </Swiper>
                </div>
            </div>
        </section>
    );
};

export default Quotes;