import React from "react";
import ScrollAnimation from "react-animate-on-scroll";

import Countdown from "../../utils/Countdown";
import DarkWaves from "../Layout/DarkWaves";
const Slogan = () => {
  return (
    <section
      id="home"
      className="slogan position-relative d-flex align-items-center"
    >
      <div className="container text-center d-flex justify-content-center flex-column home">
        <ScrollAnimation animateIn={"fadeIn"} animateOnce>
          <h1 className="display-1 d-flex align-items-center justify-content-center text-shadow">
            <div className="righteous">
              ARGENTINA{" "}
              <span className="righteous" style={{ color: "#2998ff" }}>
                2023
              </span>{" "}
              <br />
              <ScrollAnimation animateIn={"pulse"} duration={4} animateOnce>
                <h3 className="fw-bold">UN EQUIPO, UN CORAZÓN</h3>
              </ScrollAnimation>
            </div>
          </h1>
        </ScrollAnimation>

        <div>
          <div className="bg-white text-dark shadow py-2 flex-sm-row flex-column px-sm-5 px-4 mx-auto align-items-center d-inline-flex rounded-pill">
            <h3 className="me-sm-4 mb-0">
              <span className="righteous">FALTAN</span>
            </h3>
            <Countdown />
          </div>
        </div>
      </div>
      <DarkWaves />
    </section >
  );
};

export default Slogan;
