import React from "react";
import ScrollAnimation from "react-animate-on-scroll";

import confraLogo from "../../assets/logos/CONFRA_LOGO_04.png";

const AboutConfra = () => {
  return (
    <article className="py-2 overflow-hidden">
      <div className="container">
        <h1 className="righteous display-4 text-center">Que es el CONFRA?</h1>
        <div className="row pt-4">
          <div className="col-lg-6">
            <ScrollAnimation animateIn="fadeInLeft" animateOnce>
              <h4 className="righteous">Intro</h4>
              <p>
                Lorem, ipsum dolor sit amet consectetur adipisicing elit. Ipsa
                ad corporis iusto aperiam earum mollitia omnis impedit, rerum
                pariatur laudantium dolorem deleniti repellat deserunt magni
                placeat quidem tenetur adipisci iure?
              </p>
              <h4 className="righteous">Our Mission</h4>
              <p>
                Lorem ipsum dolor sit amet consectetur adipisicing elit. Quia
                dolorem voluptatibus perspiciatis mollitia autem, tempora
                accusamus animi dolores ad sequi! Earum non sit amet maxime quod
                cumque dolorem quia aspernatur!
              </p>
            </ScrollAnimation>
          </div>
          <div className="col-lg-6 px-4">
            <ScrollAnimation animateIn="fadeInRight" animateOnce>
              <div className="position-relative py-1">
                <img
                  className="w-100 "
                  src={confraLogo}
                  alt="Logo"
                />
              </div>
            </ScrollAnimation>
          </div>
        </div>
      </div>
    </article>
  );
};

export default AboutConfra;
